import React from "react";
import "./About.css";
import aboutImg from "./img/Capture2.PNG";
function About() {
  //  Up To Top Btn
  window.addEventListener("scroll", function () {
    const upToTop = document.querySelector("a.bottom__to__top");
    upToTop.classList.toggle("active", window.scrollY > 0);
  });
  return (
    <div className="about component__space" id="About">
      <div className="container">
        <div className="row">
          <div className="col__2">
            <img src={aboutImg} alt="" className="about__img" />
          </div>
          <div className="col__2">
            <h1 className="about__heading">About Us</h1>
            <div className="about__meta">
              <p className="about__text p__color">
                Laphrica media is a marketing agency that is aimed at helping
                businesses achieve dominance in their industry which means more
                revenue, profits and ability to scale.
              </p>
              <p className="about__text p__color">
                How would you feel if you would consistently and predictably
                getting more new customers for your business. With our ruthless
                marketing strategies, revenue-driven implementation and
                conversion process we give you the power to experience the
                feeling.
              </p>
              <p className="about__text p__color">
                We know success is more than just creating the right adverts.
                Its getting in the mind of your target market and implementing a
                full cold to hot journey building massive brand visibility and
                expanding your community from a digital perspective.
              </p>

              {/* <div className="about__button d__flex align__items__center">
                <a href="#">
                  <button className="about btn pointer">Download Cv</button>
                </a>
                <a href="#">
                  <button className="about btn pointer">Hire Me</button>
                </a>
              </div> */}
            </div>
          </div>
        </div>
      </div>
      {/* UP TO TOP BTN */}
      <div className="up__to__top__btn">
        <a href="#" className="bottom__to__top">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="currentColor"
            class="bi bi-chevron-up white"
            viewBox="0 0 16 16"
          >
            <path
              fill-rule="evenodd"
              d="M7.646 4.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1-.708.708L8 5.707l-5.646 5.647a.5.5 0 0 1-.708-.708l6-6z"
            />
          </svg>
        </a>
      </div>
    </div>
  );
}

export default About;
